.example {
  background-color: #f2f2f2;
  border-top: 1px solid #f2f2f2;
}

.example__wrap {
  @include mobile-center;
  width: auto;
}

.example__title {
  @include mobile-center;
  width: auto;
  padding-top: 15px;
  padding-bottom: 25px;

  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
}

.example__text {
  @include text-mobile;
  padding-bottom: 24px;
}

.example__list {
  @include list-reset;
  display: flex;
  margin-bottom: 35px;
}

.example__item {
  position: relative;
  width: calc(50% - 16px);
  margin-right: 32px;

  &:nth-last-child(1) {
    margin-right: 0;
  }
}

.example__fact {
  height: 56px;
  padding: 12px;

  font-size: 24px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;

  border: 1px solid #cdcdcd;
  border-radius: 3px;
}

.example__clarification {
  position: absolute;
  top: 35px;
  left: 50%;
  transform: translateX(-50%);

  padding-left: 10px;
  padding-right: 10px;

  color: #444444;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  text-align: center;

  background-color: #f2f2f2;
}

.example__cost {
  display: block;
  margin: 0;

  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
}

@media (min-width: $tablet-width) {
  .example {
    background-color: #ffffff;
    border-top: 1px solid #e6e6e6;
  }

  .example__title {
    @include tablet-center;
    padding-top: 42px;
    padding-bottom: 65px;

    font-size: 60px;
    line-height: 60px;
  }

  .example__wrap {
    @include tablet-center;
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 100px;
  }

  .example__text {
    @include text-tablet;
    width: 100%;
    padding-bottom: 53px;
    padding-right: 20px;
  }

  .example__list {
    margin-bottom: 0;
  }

  .example__item {
    margin-right: 42px;

    &:nth-last-child(1) {
      margin-right: 0;
    }
  }

  .example__fact {
    width: 162px;
    height: 72px;
    padding-top: 15px;

    font-size: 30px;
    line-height: 37px;
  }

  .example__clarification {
    top: 50px;

    font-size: 14px;
    line-height: 14px;
    white-space: nowrap;

    background-color: #ffffff;
  }

  .example__cost {
    width: 190px;
    margin-left: auto;
    padding-top: 10px;

    font-size: 20px;
    text-align: right;
  }

  .example__cost-text {
    display: inline-block;
    padding-bottom: 10px;
  }
}

@media (min-width: $desktop-width) {
  .example {
    margin: auto;
    padding: 0;
    border: none;
    background-color: #f2f2f2;
  }

  .example__title-bg {
    background: #ffffff;
  }

  .example__title {
    @include desktop-center;
    padding-top: 40px;
    padding-bottom: 75px;
  }

  .example__bg {
    @include desktop-center;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .example__wrap {
    position: relative;

    display: flex;
    flex-direction: column;
    width: 610px;
    margin: 0;
    padding: 0;
    padding-bottom: 65px;

    background-color: #f2f2f2;
  }

  .example__text {
    margin-bottom: 49px;
    padding: 20px;
    padding-left: 0;
    max-width: 455px;
  }

  .example__list {
    margin-bottom: 50px;
  }

  .example__item {
    width: 162px;
  }

  .example__clarification {
    background-color: #f2f2f2;
  }

  .example__fact {
    margin-right: 0;
  }

  .example__cost {
    width: 100%;
    margin: 0;
    margin-top: auto;

    font-size: 20px;
    text-align: left;
  }

  .example__cost-text {
    padding-right: 60px;
  }
}
