.catalog {
  margin-bottom: 42px;
}

.catalog__list {
  @include list-reset;
}

.catalog__item {
  margin: auto;
  padding-bottom: 24px;

  border: 1px solid #e6e6e6;
  border-bottom: none;

  &:nth-last-child(1) {
    border-bottom: 1px solid #e6e6e6;
  }
}

@media (min-width: $tablet-width) {
  .catalog {
    margin-bottom: 90px;
  }

  .catalog__list {
    @include tablet-center;
    display: flex;
    flex-wrap: wrap;
  }

  .catalog__item {
    display: flex;
    flex-direction: column;
    margin: 0;
    width: 324px;
    min-height: 430px;
    margin-top: 141px;
    margin-right: 60px;
    padding-bottom: 0;

    background-color: #f2f2f2;
    border: none;

    &:nth-child(2n) {
      margin-right: 0;
    }

    .catalog__button {
      margin-top: auto;
      margin-bottom: 40px;
      width: calc(100% - 74px);
    }
  }
}

@media (min-width: $desktop-width) {
  .catalog {
    margin-bottom: 97px;
  }

  .catalog__list {
    @include desktop-center;
  }

  .catalog__item {
    width: 245px;
    margin-right: 80px;

    &:nth-child(2n) {
      margin-right: 80px;
    }

    &:nth-child(4n) {
      margin-right: 0;
    }
  }
}
