.logo {
  display: flex;
  align-items: center;
  height: 66px;
  padding-right: 51px;
}

.logo__img-link,
.logo__text-link {
  &[href]:hover,
  &[href]:focus {
    opacity: 0.8;
  }

  &[href]:active {
    opacity: 0.6;
  }
}

.logo__img-link {
  margin-left: 20px;
}

.logo__text-link {
  padding-top: 12px;
  padding-bottom: 10px;
  margin: auto;
}

@media (min-width: $tablet-width) {
  .logo {
    height: 110px;
    padding: 0;
  }

  .logo__img {
    width: 173px;
    height: 58px;
  }

  .logo__img-link {
    margin: 0;
  }

  .logo__text {
    display: none;
  }
}

@media (min-width: $desktop-width) {
  .logo {
    height: 170px;
  }

  .logo__img {
    width: 202px;
    height: 59px;
  }
}
