.social__list-wrap {
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.social__list {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  max-width: 220px;
  margin: auto;
  padding-top: 10px;
  padding-bottom: 10px;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
}

.social__item {
  display: block;
  font-size: 0;
  width: 40px;
  height: 40px;
  margin-right: 15px;

  background-repeat: no-repeat;
  background-position: 50% 50%;
  opacity: 0.6;

  &:nth-last-child(1) {
    margin-right: 0;
  }
}

.social__svg {
  fill: #000000;
  transition: fill 0.2s ease-out;

  &:hover,
  &:focus {
    fill: $color-main;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: $tablet-width) {
  .social__list-wrap {
    border: none;
  }

  .social__list {
    max-width: 290px;
    margin-left: 25px;
  }

  .social__link-vk {
    width: 30px;
    height: 16px;
  }

  .social__link-insta {
    width: 23px;
    height: 23px;
  }

  .social__link-fb {
    width: 14px;
    height: 28px;
  }
}
