body {
  min-width: 320px;
  margin: 0;

  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  color: $text-color-main;

  background-color: #ffffff;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.button {
  @include link-style;

  display: block;
  padding: 10px;
  margin-left: 20px;
  margin-right: 20px;

  color: #ffffff;
  font-size: 16px;
  line-height: 20px;
  text-align: center;

  background-color: $color-main;

  &:hover {
    background-color: #5eaa2f;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

.js--transition {
  transition: 1.5s ease-out;
}
