.form-contacts {
  position: relative;
  padding-top: 65px;
  padding-bottom: 41px;
}

.form-contacts__label {
  position: relative;
  margin-bottom: 14px;

  &:last-child {
    margin-bottom: 0;
  }
}

.form-contacts__input + svg {
  position: absolute;
  top: 16px;
  right: 16px;
}

.form-contacts__input--tel + svg {
  top: 12px;
  right: 13px;
}

.form-contacts__input:focus:invalid + svg,
.form-contacts__input:active + svg {
  fill: #ff8282;
}

.form-contacts__input:focus:valid + svg,
.form-contacts__input:active:valid + svg {
  fill: #68b738;
}

@media (min-width: $tablet-width) {
  .form-contacts {
    padding-top: 87px;
    padding-bottom: 87px;
    padding-right: 65px;
  }

  .form-contacts__label {
    margin-bottom: 20px;
  }

  .form-contacts__input + svg {
    position: absolute;
    top: 20px;
    right: 21px;
  }

  .form-contacts__input--tel + svg {
    top: 17px;
    right: 18px;
  }

  .form-contacts__title br {
    display: none;
  }
}

@media (min-width: $desktop-width) {
  .form-contacts {
    @include desktop-center;
    padding-bottom: 69px;
  }

  .form-contacts__field-wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .form-contacts__label {
    width: 570px;
  }

  .form-contacts__label:nth-child(2n) {
    margin-bottom: 20px;
  }
}
