.extra {
  margin-bottom: 35px;
  border-top: 3px solid $color-main;
}

.extra__center {
  @include mobile-center;
  position: relative;
  width: 100%;
  padding-top: 41px;
}

.extra__title {
  position: absolute;
  top: -18px;
  left: 0;

  margin: 0;
  padding: 0 20px;

  font-size: 24px;
  font-weight: 400;
  line-height: 24px;

  background-color: #ffffff;
}

.extra__list {
  @include list-reset;
  border-top: 1px solid #cdcdcd;
  border-collapse: collapse;
  width: 100%;
  table-layout: fixed;
}

.extra__item {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  border-bottom: 1px solid #cdcdcd;
}

.extra__cell {
  padding: 0;
}

.extra__name {
  width: 100%;
  display: block;
  margin: 0;
  padding: 12px 1px;

  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
}

.extra__amount,
.extra__cost {
  @include text-mobile;
  width: 50%;
  display: block;
}

.extra__cost {
  text-align: right;
}

.extra__button-wrap {
  width: 100%;
  display: block;
  margin-top: 13px;
  margin-bottom: 17px;
}

.extra__button {
  width: 100%;
  margin: 0;
}

.extra__button-link {
  @include link-style;
}

.extra__present {
  position: relative;

  display: block;
  margin: auto;
  min-height: 289px;
  margin-top: 40px;

  background-color: #7dc154;
  background-image: url("../../img/gift-photo-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;

  &::before {
    content: "";
    position: absolute;
    top: 65px;
    left: calc(50% - 55px / 2);

    width: 55px;
    height: 60px;

    background-image: url("../../img/icon-gift.svg");
    background-position: 50% 50%;
    background-repeat: no-repeat;
  }
}

.extra__present-text {
  margin: 0;
  padding-top: 186px;
  padding-left: 58px;
  padding-right: 58px;

  color: #ffffff;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .extra__center {
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 90px;
  }

  .extra__title {
    top: -25px;
    left: 30px;

    padding-left: 35px;
    padding-right: 35px;

    font-size: 36px;
    line-height: 36px;
  }

  .extra__item {
    align-items: baseline;
    padding-top: 16px;
    padding-left: 37px;
    padding-right: 37px;

    border: 1px solid #d9d9d9;
    border-top: none;
  }

  .extra__name {
    max-width: 325px;
    font-size: 20px;
  }

  .extra__amount,
  .extra__cost {
    width: auto;
    font-size: 16px;
    line-height: 20px;
  }

  .extra__amount {
    margin-left: auto;
  }

  .extra__cost {
    width: 95px;
    margin-left: 0;
  }

  .extra__button-wrap {
    width: 100%;
    margin-top: 15px;
    margin-bottom: 27px;
  }

  .extra__button {
    width: 100%;
  }

  .extra__present {
    min-height: 200px;
    margin-top: 70px;
    margin-bottom: 75px;

    background-image: url("../../img/gift-photo-tablet@1x.jpg");
    background-color: $color-main;

    &::before {
      content: "";
      position: absolute;
      top: 65px;
      left: 90px;

      width: 55px;
      height: 60px;

      background-image: url("../../img/icon-gift.svg");
      background-position: 50% 50%;
      background-repeat: no-repeat;
    }
  }

  .extra__present-text {
    padding-top: 72px;
    padding-left: 422px;
    padding-right: 100px;
    text-align: left;
  }

  @media (min-width: 950px) {
    .extra__center {
      max-width: 1100px;
    }

    .extra__amount {
      text-align: left;
      padding-top: 9px;
      margin-left: 0;
      margin-right: auto;
    }

    .extra__cost {
      padding-top: 9px;
      width: 81px;
      text-align: left;
    }

    .extra__button-wrap {
      width: 245px;
      margin: 0;
      margin-top: 17px;
      margin-bottom: 17px;
    }
  }

  @media (min-width: $desktop-width) {
    .extra {
      margin-bottom: 90px;
    }

    .extra__center {
      @include desktop-center;
      max-width: none;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    .extra__title {
      left: 70px;
      padding-left: 40px;
      padding-right: 40px;
      font-size: 36px;
    }

    .extra__list {
      width: 895px;
    }

    .extra__item {
      align-items: center;
      padding: 0;
      border-left: none;
      border-right: none;
    }

    .extra__amount {
      text-align: left;
      padding-top: 9px;
      margin-left: 0;
      margin-right: auto;
    }

    .extra__cost {
      padding-top: 9px;
      width: 81px;
      text-align: left;
    }

    .extra__button-wrap {
      width: 245px;
      margin: 0;
      margin-top: 15px;
      margin-bottom: 15px;
    }

    .extra__present {
      margin: 0;
      width: 245px;
      min-height: 289px;
      background-image: url("../../img/gift-photo-desktop@1x.jpg");
    }

    .extra__present::before {
      top: 65px;
      left: 96px;
    }

    .extra__present-text {
      padding: 0;
      padding-top: 189px;
      padding-left: 42px;
      padding-right: 42px;
      text-align: center;
    }
  }
}
