.page-catalog__title-wrap {
  border-top: 1px solid #e6e6e6;
}

.page-catalog__title {
  @include mobile-center;
  padding-top: 15px;
  padding-bottom: 28px;

  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
}

@media (min-width: $tablet-width) {
  .page-catalog__title-wrap {
    border-top: none;
  }

  .page-catalog__title {
    width: $tablet-width;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 24px;
    padding-top: 30px;

    font-size: 60px;
  }
}

@media (min-width: 1092px) {
  .page-catalog__title {
    width: 1092px;
  }
}

@media (min-width: $desktop-width) {
  .page-catalog__title {
    width: $desktop-width;
    padding-top: 20px;
    padding-left: 110px;
    padding-right: 110px;
  }
}
