.copyright {
  display: flex;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

.copyright__link {
  display: flex;
  width: 100%;
  align-items: flex-end;
  justify-content: space-between;
  margin-left: auto;

  text-decoration: none;

  &:hover .copyright__svg {
    fill: $color-main;
    transition: fill 0.2s ease-out;
  }

  &:active {
    opacity: 0.3;
  }
}

.copyright__text {
  margin: 0;
  padding-top: 10px;
  padding-right: 23px;
  padding-bottom: 10px;

  color: #444444;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
}

.copyright__svg {
  fill: #666666;
}
