.form-programs {
  padding-top: 20px;
  padding-bottom: 20px;
  border: 1px solid #e6e6e6;
}

.form-programs__label {
  display: flex;
  position: relative;
  padding-left: 45px;
  padding-top: 6px;
  padding-bottom: 6px;
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }
}

.form-programs__filter + label::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  width: 30px;
  height: 30px;

  border: 1px solid #cecece;
  border-radius: 50%;
}

.form-programs__filter:hover + label::before,
.form-programs__filter:focus + label::before {
  border: 1px solid #bcbcbc;
}

.form-programs__filter:active + label {
  opacity: 0.3;
}

.form-programs__filter:active + label::before {
  opacity: 0.3;
}

.form-programs__filter:checked + label::after {
  content: "";
  position: absolute;
  top: 9px;
  left: 9px;

  width: 12px;
  height: 12px;
  background-color: $color-main;
  border-radius: 50%;
}

.form-programs__filter:checked:active + label::after {
  background-color: #d1e9c3;
}

@media (min-width: $tablet-width) {
  .form-programs {
    width: auto;
    padding-left: 28px;
    padding-right: 0;
    padding-top: 25px;
    padding-bottom: 25px;
    margin-left: 70px;
    margin-right: 70px;
  }

  .form-programs label {
    padding-left: 60px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .form-programs__filter + label::before {
    width: 40px;
    height: 40px;
  }

  .form-programs__filter:checked + label::after {
    top: 11px;
    left: 11px;

    width: 18px;
    height: 18px;
  }

  @media (min-width: $desktop-width) {
    .form-programs {
      margin: 0;
      width: 570px;
    }
  }
}
