.slider {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 210px;
  margin: auto;
  padding-bottom: 10px;

  text-align: center;
}

.slider__button {
  padding: 5px;
  font-family: inherit;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;

  background-color: transparent;
  border: none;
}

.slider__scale {
  width: 83px;
  height: 14px;

  border: 1px solid #cdcdcd;
  border-radius: 7px;
  background-color: #ffffff;
}

.slider__pointer {
  width: 36px;
  height: 6px;
  margin-top: 3px;
  margin-left: 5px;

  background-color: $color-main;
  border-radius: 3px;
}

@media (min-width: $tablet-width) {
  .gallery-bg {
    background-color: #f2f2f2;
  }

  .gallery {
    width: 610px;
    padding-bottom: 59px;
  }

  .gallery__img {
    width: 690px;
    max-height: 522px;
  }

  .slider {
    width: 570px;
    margin-top: 30px;
  }

  .slider__button {
    padding: 0;
    font-size: 20px;
    line-height: 30px;
    z-index: 1;
    border-bottom: 1px dotted #bebebe;

    &:hover,
    &:focus {
      border-bottom: 1px solid #989898;
    }

    &:active {
      border-bottom-color: transparent;
    }
  }

  .slider__scale {
    position: relative;
    width: 428px;
    height: 6px;
    background-color: #dcdcdc;
    border: none;
  }

  .slider__pointer {
    position: absolute;
    width: 34px;
    height: 34px;
    top: -17px;
    left: 50%;
    transform: translateX(-50%);

    margin-left: 0;

    border: 1px solid #cdcdcd;
    border-radius: 50%;
    background-color: #ffffff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);

    &::after {
      position: absolute;
      content: "";
      top: 12px;
      left: 12px;
      width: 8px;
      height: 8px;

      border-radius: 50%;
      background-color: $color-main;
    }

    &:hover {
      box-shadow: 0 3px 5px rgba(0, 0, 0, 0.25);
    }

    &:active {
      border: 2px solid $color-main;
      box-shadow: 0 4px 5px rgba(0, 0, 0, 0.3);
    }

    &:active::after {
      top: 11px;
      left: 11px;
    }
  }
}

@media (min-width: $desktop-width) {
  .gallery {
    padding-bottom: 0;
  }
}
