.show-all {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid #ededed;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 40px;
    left: calc(50% - (30px / 2));

    width: 30px;
    height: 2px;

    background-color: #d3d3d3;
  }

  &::after {
    transform: rotate(90deg);
  }
}

.show-all__title {
  margin: 0;
  margin-top: 75px;
  padding: 5px;

  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}

.show-all__comment {
  padding: 3px;
  margin-bottom: 17px;

  color: #444444;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
}

.show-all__button {
  color: #444444;
  background-color: #f2f2f2;
  width: calc(100% - 40px);

  &:hover {
    background-color: #ebebeb;
  }

  &:active {
    color: #444444;
    opacity: 0.3;
  }
}

@media (min-width: $tablet-width) {
  .show-all {
    padding-left: 37px;
    padding-right: 37px;

    background-color: #ffffff;
    border: 1px solid #e6e6e6;

    &::before,
    &::after {
      top: 85px;
      left: calc(50% - 30px);
      width: 60px;
    }
  }

  .show-all__title {
    max-width: 100%;
    margin: 0;
    padding-top: 165px;
    padding-bottom: 20px;
    padding-left: 40px;
    padding-right: 40px;

    font-size: 20px;
    text-align: center;
  }

  .show-all__comment {
    max-width: 100%;
    padding-left: 30px;
    padding-right: 30px;

    font-size: 16px;
    line-height: 19px;
    text-align: center;
  }

  .show-all__button {
    width: 100%;
    margin-top: auto;
    margin-bottom: 37px;
  }
}

@media (min-width: $desktop-width) {
  .show-all__title {
    padding-left: 10px;
    padding-right: 10px;
  }

  .show-all__comment {
    padding: 5px 0;
  }
}
