.form-extra {
  position: relative;
  padding-top: 42px;
  padding-bottom: 35px;
}

.form-extra__label {
  position: relative;
  padding-left: 46px;
  padding-top: 6px;
  padding-bottom: 6px;
}

.form-extra__checkbox-wrap {
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }
}

.form-extra__checkbox + label::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  width: 30px;
  height: 30px;

  border: 1px solid #cfcfcf;
}

.form-extra__checkbox:hover + label::before,
.form-extra__checkbox:focus + label::before {
  border: 1px solid #bababa;
}

.form-extra__checkbox:active + label::before {
  border: 1px solid #eaeaea;
}

.form-extra__checkbox:active + label {
  opacity: 0.3;
}

.form-extra__checkbox:checked + label::before {
  background-image: url("../../img/icon-tick.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 21px 16px;
}

@media (min-width: $tablet-width) {
  .form-extra {
    padding-top: 68px;
    padding-bottom: 22px;
  }

  .form-extra__container {
    display: flex;
    flex-wrap: wrap;
  }

  .form-extra__checkbox-wrap {
    width: 284px;
    margin-right: 60px;
    margin-bottom: 32px;

    &:nth-child(2n) {
      margin-right: 0;
    }
  }

  .form-extra__label {
    position: relative;
    padding-left: 58px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .form-extra__checkbox + label::before {
    width: 40px;
    height: 40px;
  }

  @media (min-width: $desktop-width) {
    .form-extra {
      padding-bottom: 29px;
    }

    .form-extra__container {
      @include desktop-center;
    }

    .form-extra__checkbox-wrap {
      width: 245px;
      margin-right: 80px;

      &:nth-child(2n) {
        margin-right: 80px;
      }

      &:nth-child(4n) {
        margin-right: 0;
      }
    }
  }
}
