.gallery {
  @include mobile-center;

  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  width: 320px;

  padding-left: 0;
  padding-right: 0;
  padding-bottom: 24px;
}

.gallery__resize {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 50%;
  overflow: hidden;
}

.gallery__img {
  max-width: none;
  background-color: #f2f2f2;
}

@media (min-width: $tablet-width) {
  .gallery-bg {
    background-color: #f2f2f2;
  }

  .gallery {
    width: 610px;
    padding-bottom: 59px;
  }

  .gallery__img {
    width: 690px;
    max-height: 522px;
  }
}

@media (min-width: $desktop-width) {
  .gallery {
    padding-bottom: 0;
  }
}
