.form-comment {
  position: relative;
  padding-top: 42px;
  padding-bottom: 40px;
}

.form-comment__text {
  width: 100%;
  min-height: 150px;
  padding: 12px;
  padding-left: 15px;

  color: #444444;
  font: inherit;
  text-transform: uppercase;

  border: 1px solid #e7e7e7;
  resize: none;
  overflow: hidden;
}

@media (min-width: $tablet-width) {
  .form-comment {
    padding-top: 87px;
    padding-bottom: 86px;
  }

  .form-comment__text {
    min-height: 174px;
    padding-top: 15px;
    padding-left: 18px;
  }
}

@media (min-width: $desktop-width) {
  .form-comment {
    @include desktop-center;
    padding-bottom: 88px;
  }
}
