.programms {
  margin: auto;
}

.programms__list {
  @include list-reset;
  padding-left: 20px;
  padding-right: 20px;
}

.programms__item {
  background-color: #f2f2f2;
  margin-bottom: 20px;

  &:nth-last-child(1) {
    margin-bottom: 0;
  }
}

.programms__item-wrap {
  padding-top: 25px;
  padding-left: 20px;
  padding-right: 20px;
}

.programms__title {
  position: relative;
  margin: 0;
  padding-top: 5px;
  padding-bottom: 30px;
  padding-left: 71px;

  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;
}

.programms__title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 2px;

  display: block;
  width: 50px;
  height: 50px;

  background-color: $color-main;
  background-repeat: no-repeat;
  border-radius: 50%;
}

.programms__item--slim .programms__title::after {
  content: "";
  position: absolute;
  top: -3px;
  left: 10px;

  display: block;
  width: 49px;
  height: 49px;

  background-image: url("../img/icon-cat-slim.svg");
  background-repeat: no-repeat;
}

.programms__item--pro .programms__title::after {
  content: "";
  position: absolute;
  top: 10px;
  left: -9px;

  display: block;
  width: 70px;
  height: 30px;

  background-image: url("../img/icon-cat-pro.svg");
  background-repeat: no-repeat;
}

.programms__text {
  @include text-mobile;
  padding-bottom: 21px;

  border-bottom: 1px solid #d9d9d9;
}

.programms__arrow {
  position: relative;
  display: inline-block;

  &::before {
    content: "";
    position: absolute;
    top: -13px;
    left: 12px;

    display: block;
    width: 24px;
    height: 11px;

    background-image: url("../img/icon-right-arrow-long.svg");
    background-repeat: no-repeat;
  }
}

.programms__link {
  @include link-style;

  display: inline-block;
  padding-top: 20px;
  padding-right: 50px;
  padding-bottom: 21px;
  padding-left: 0;

  font-size: 16px;
  line-height: 16px;

  &:hover .programms__arrow::before,
  &:focus .programms__arrow::before {
    width: 33px;
    background-image: url("../img/icon-right-arrow-verylong.svg");
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: $tablet-width) {
  .programms {
    margin-bottom: 30px;
  }

  .programms__list {
    padding-left: 30px;
    padding-right: 30px;
  }

  .programms__item {
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .programms__item-wrap {
    padding-top: 45px;
    padding-bottom: 40px;
    padding-left: 52px;
    min-height: 372px;
  }

  .programms__title::before {
    top: 47px;
    left: 394px;

    width: 200px;
    height: 200px;
  }

  .programms__item--slim .programms__title::after {
    top: 35px;
    left: 424px;

    width: 140px;
    height: 194px;
  }

  .programms__item--pro .programms__title::after {
    top: 90px;
    left: 360px;

    width: 268px;
    height: 112px;
  }

  .programms__title {
    padding-left: 0;
    padding-top: 30px;
    padding-bottom: 25px;

    font-size: 36px;
    line-height: 37px;
  }

  .programms__text {
    @include text-tablet;
    min-height: 134px;
    padding-bottom: 20px;

    border: none;
  }

  .programms__item--slim .programms__text {
    width: 372px;
  }

  .programms__item--pro .programms__text {
    width: 340px;
  }

  .programms__link {
    padding-right: 70px;
    font-size: 20px;
    line-height: 20px;
  }

  .programms__arrow::before {
    top: -15px;
    left: 24px;

    width: 33px;
    height: 11px;
  }
}

@media (min-width: $desktop-width) {
  .programms {
    width: $desktop-width;
    margin-bottom: 0;
  }

  .programms__list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 40px;
  }

  .programms__item {
    display: flex;
    margin-right: 80px;

    &:nth-child(2n) {
      margin-right: 0;
    }

    &:last-child {
      margin-bottom: 30px;
    }
  }

  .programms__item-wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 570px;
    margin-bottom: 0;
    padding-top: 47px;
  }

  .programms__title::before {
    top: 0;
    left: 0;

    width: 100px;
    height: 100px;
  }

  .programms__item--slim .programms__title::after {
    top: -6px;
    left: 14px;

    width: 70px;
    height: 97px;
  }

  .programms__item--pro .programms__title::after {
    top: 22px;
    left: -18px;

    width: 134px;
    height: 56px;
  }

  .programms__title {
    margin-bottom: 48px;
    padding-left: 162px;
  }

  .programms__text {
    min-height: 50px;
    padding-bottom: 10px;
  }

  .programms__item--slim .programms__text {
    width: 470px;
  }

  .programms__item--pro .programms__text {
    width: 470px;
  }

  .programms__link {
    margin-top: auto;
  }
}
