@media (min-width: $tablet-width) {
  .page-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (min-width: $desktop-width) {
  .page-header {
    @include desktop-center;
  }

  .page-header--index {
    position: absolute;
    top: 0;
    left: 50%;
    padding-bottom: 0;
    margin-left: -650px;
  }
}
