.advantages {
  margin: auto;
  margin-bottom: 42px;
  padding-left: 20px;
  padding-right: 20px;
}

.advantages__title {
  margin: 0;
  padding-top: 17px;
  padding-bottom: 28px;

  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
}

.advantages__list {
  @include list-reset;
}

.advantages__item {
  position: relative;

  margin-bottom: 20px;
  padding-left: 80px;

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    z-index: -2;

    width: 60px;
    height: 60px;

    background-color: $color-main;
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }
}

.advantages__item--nutrients::before {
  background-image: url("../img/advantage-leaf@1x.png");
}

.advantages__item--simple::before {
  background-image: url("../img/advantage-powder@1x.png");
}

.advantages__item--food::before {
  background-image: url("../img/advantage-dining@1x.png");
}

.advantages__item--time::before {
  background-image: url("../img/advantage-clock@1x.png");
}

.advantages__text {
  @include text-mobile;
  min-height: 60px;
  padding-top: 2px;
}

@media (min-width: $tablet-width) {
  .advantages {
    @include tablet-center;
    margin-bottom: 46px;
  }

  .advantages__title {
    margin-bottom: 42px;
    font-size: 60px;
  }

  .advantages__list {
    display: flex;
    flex-wrap: wrap;
  }

  .advantages__item {
    width: 245px;
    min-height: 240px;
    margin-right: 139px;
    margin-bottom: 77px;
    padding-top: 110px;
    padding-left: 0;

    background-repeat: no-repeat;
    background-position: 125px 0;

    &:nth-child(2n) {
      margin-right: 0;
    }

    &:nth-last-child(1),
    &:nth-last-child(2) {
      margin-bottom: 0;
    }

    &::before {
      width: 80px;
      height: 80px;
    }
  }

  .advantages__item--nutrients {
    background-image: url("../img/1.png");
  }

  .advantages__item--simple {
    background-image: url("../img/2.png");
  }

  .advantages__item--food {
    background-image: url("../img/3.png");
  }

  .advantages__item--time {
    background-image: url("../img/4.png");
  }

  .advantages__text {
    @include text-tablet;
  }
}

@media (min-width: $desktop-width) {
  .advantages {
    @include desktop-center;
  }

  .advantages__title {
    margin-bottom: 44px;
    padding-top: 43px;
  }

  .advantages__item {
    width: 245px;
    margin-right: 80px;
    margin-bottom: 30px;

    &:nth-child(2n) {
      margin-right: 80px;
    }

    &:nth-child(4n) {
      margin-right: 0;
    }
  }
}
