.product__about {
  @include mobile-center;
  display: flex;
}

.product__img-full {
  width: 140px;
  flex-shrink: 0;
}

.product__img {
  display: block;
  width: 100%;
}

.product__description {
  border-collapse: collapse;
}

.product__title {
  @include link-style;

  display: block;
  padding-right: 40px;
  padding-top: 22px;
  padding-bottom: 12px;

  font-size: 16px;
  line-height: 20px;
  text-align: left;
}

.product__property,
.product__value {
  @include text-mobile;

  padding: 0;
  width: 70px;

  font-size: 12px;
  line-height: 16px;

  border: none;
}

.product__value {
  text-align: right;
}

@media (min-width: $tablet-width) {
  .product__about {
    flex-direction: column;
    margin: 0;
    padding-left: 37px;
    padding-right: 37px;
    width: auto;
  }

  .product__img-full {
    position: relative;
    width: 324px;
    height: auto;
    margin-left: -20px;
  }

  .product__img {
    position: absolute;
    bottom: -127px;
    left: -17px;
  }

  .product__description {
    width: 100%;
  }

  .product__title {
    padding-top: 163px;
    padding-bottom: 18px;
    padding-left: 50px;
    padding-right: 50px;

    font-size: 20px;
    text-align: center;
  }

  .product__property,
  .product__value {
    font-size: 16px;
    padding-top: 8px;
    padding-bottom: 7px;
    border-bottom: 1px solid #cdcdcd;
  }

  .product__button {
    margin: auto;
    margin-bottom: 39px;
    width: 250px;
  }
}

@media (min-width: $desktop-width) {
  .product__img-full {
    width: 245px;
  }

  .product__about {
    width: 100%;
    padding-left: 38px;
    padding-right: 38px;
  }

  .product__title {
    padding-left: 10px;
    padding-right: 10px;
  }

  .product__button {
    width: 170px;
  }
}
