.page-footer {
  background-color: #f2f2f2;
}

.page-footer__wrap {
  padding-left: 20px;
  padding-right: 20px;
}

.page-footer__logo-img {
  display: block;
  margin: auto;
  padding-top: 40px;
  padding-bottom: 31px;
}

.page-footer__logo-link[href]:hover {
  opacity: 0.8;
}

.page-footer__logo-link[href]:active {
  opacity: 0.6;
}

@media (min-width: $tablet-width) {
  .page-footer {
    min-height: 150px;
  }

  .page-footer__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 30px;
    padding-top: 25px;
  }

  .page-footer__logo-link {
    border: none;
  }
}

@media (min-width: $desktop-width) {
  .page-footer__wrap {
    @include desktop-center;
  }
}
