.form {
  margin-bottom: 15px;
  border-top: 1px solid #e6e6e6;
}

.form__description {
  @include mobile-center;
}

.form__title {
  margin: 0;
  padding-top: 15px;
  padding-bottom: 25px;

  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
}

.form__text {
  margin: 0;
  padding-right: 40px;
  padding-bottom: 38px;

  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}

.form__section {
  @include mobile-center;
  padding-bottom: 25px;
  padding-top: 0;

  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;

  border: none;
}

.form__section label {
  display: flex;
  align-items: center;
}

.form__section input:not([type="checkbox"]):not([type="radio"]) {
  flex-grow: 1;
  padding: 12px;
  padding-bottom: 13px;
  padding-left: 13px;
  margin-left: 13px;

  font: inherit;
  color: #444444;
  text-transform: uppercase;

  border: 1px solid #e6e6e6;

  &:hover {
    border: 1px solid #cdcdcd;
  }
}

.form__section textarea {
  &:hover {
    border: 1px solid #cdcdcd;
  }
}

.form__section input,
.form__section textarea {
  &:focus:valid {
    outline: 2px solid #444444;
  }

  &:active:invalid,
  &:focus:invalid {
    outline: 2px solid #ff8282;
  }
}

.form__section legend {
  position: absolute;
  top: -17px;
  left: 0;

  padding-left: 20px;
  padding-right: 20px;

  font-size: 24px;
  line-height: 24px;
  text-transform: none;

  background-color: #ffffff;
}

.form__top {
  margin-bottom: 43px;
}

.form__contacts-wrap {
  border-top: 3px solid $color-main;
}

.form__comment-wrap {
  border-top: 3px solid $color-main;
}

.form__extra-wrap {
  border-top: 3px solid $color-main;
}

.form__button {
  width: calc(100% - 40px);
  border: none;
  font-size: 16px;
  font-family: "Oswald", "Arial", sans-serif;
}

.form__small {
  display: block;
  padding-top: 10px;
  padding-bottom: 15px;
  margin-top: 10px;

  color: #444444;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .form {
    margin-bottom: 72px;
    border: none;
  }

  .form__description {
    @include tablet-center;
  }

  .form__title {
    padding-top: 30px;
    padding-bottom: 45px;

    font-size: 60px;
    line-height: 60px;

    border: none;
  }

  .form__text {
    max-width: 370px;
    padding-left: 40px;
    padding-right: 0;
    padding-top: 10px;
    padding-bottom: 65px;

    font-size: 20px;
    line-height: 30px;
  }

  .form__section {
    @include tablet-center;
    padding-left: 70px;
    padding-right: 70px;
    padding-bottom: 70px;

    font-size: 20px;
    line-height: 20px;
  }

  .form__section legend {
    top: -25px;
    left: 0;

    margin-left: 30px;
    padding-left: 40px;
    padding-right: 40px;

    font-size: 36px;
    line-height: 36px;
  }

  .form__top {
    margin-bottom: 100px;
  }

  .form__section input:not([type="checkbox"]):not([type="radio"]) {
    padding: 9px;
    padding-bottom: 11px;
    padding-left: 18px;
    margin-left: 17px;
  }

  .form__button-wrap {
    display: flex;
    align-items: center;
    width: $tablet-width;
    margin: auto;
    padding-left: 70px;
    padding-right: 70px;
  }

  .form__button {
    width: 284px;
    margin: 0;
    margin-right: 60px;
    padding: 16px;

    font-size: 20px;
    line-height: 20px;
  }

  .form__small {
    margin: 0;
    font-size: 16px;
  }
}

@media (min-width: $desktop-width) {
  .form {
    margin-bottom: 80px;
  }

  .form__description {
    @include desktop-center;
  }

  .form__title {
    padding-top: 20px;
  }

  .form__text {
    margin-left: 0;
    padding-left: 0;
  }

  .form__top {
    @include desktop-center;
    display: flex;
    margin-bottom: 92px;
  }

  .form__section {
    @include desktop-center;
    padding: 0;
  }

  .form__section legend {
    margin-left: 70px;
  }

  .form-info {
    width: 570px;
    margin-right: 80px;
  }

  .form__button-wrap {
    @include desktop-center;
  }

  .form__button {
    width: 570px;
    margin-right: 80px;
  }
}
