.location {
  width: auto;
  margin: auto;
  padding-bottom: 0;
}

.location__description-bg--white {
  background-color: #ffffff;
}

.location__description-bg--gray {
  background-color: #f2f2f2;
}

.location__description {
  display: flex;
  justify-content: space-between;
  padding-top: 29px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 25px;
}

.location__text {
  width: 140px;

  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}

.location__address {
  @include text-mobile;
  width: 140px;
  font-style: normal;
  line-height: 20px;
}

.location__map {
  display: block;
  width: 100%;
  height: 362px;

  background-color: #f2f2f2;
  background-image: url("../../img/map-pin.png"), url("../../img/bg-map-mobile@1x.png");
  background-repeat: no-repeat, no-repeat;
  background-position: 50% 45%, 0 -38px;
  background-size: 60px 53px, 100%;
}

@media (min-width: $tablet-width) {
  .location__description {
    width: $tablet-width;
    margin: auto;
    padding: 65px 70px;
    padding-bottom: 58px;
    display: flex;
    justify-content: space-between;
  }

  .location__text {
    width: 170px;
    font-size: 20px;
    line-height: 25px;
  }

  .location__address {
    font-size: 16px;
    width: 175px;
    text-align: right;
  }

  .location__city {
    display: block;
    padding-top: 18px;
  }

  .location__map {
    height: 396px;

    background-image: url("../../img/map-pin.png"), url("../../img/bg-map-tablet@1x.png");
    background-position: 50% 50%, 0 -5px;
    background-size: 125px 106px, 100%;
  }
}

@media (min-width: $desktop-width) {
  .location {
    position: relative;
  }

  .location__description {
    position: absolute;
    top: 95px;
    left: 50%;
    z-index: 1;

    transform: translateX(-610px);

    width: 570px;
    min-height: 200px;
    padding-left: 75px;

    background-color: #ffffff;
  }

  .location__address {
    text-align: left;
  }

  .location__map {
    height: 400px;

    background-image: url("../../img/map-pin.png"), url("../../img/bg-map-desktop@1x.png");
    background-position: 75.75% 50%, 0 -5px;
    background-size: 124px 106px, 100%;
  }
}
