.main-nav {
  position: relative;
}

.main-nav__toggle {
  position: absolute;
  top: -65px;
  right: 0;
  z-index: 2;

  width: 65px;
  height: 65px;
  padding: 0;

  background-color: transparent;
  border: none;
  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 22px;
  left: 21px;

  width: 24px;
  height: 2px;

  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}

.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 32px;
    left: 22px;

    width: 22px;
    height: 2px;

    background-color: black;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.main-nav__list {
  @include list-reset;
  border-top: 1px solid #e6e6e6;
}

.main-nav__item {
  border-bottom: 1px solid #e6e6e6;

  &:nth-last-child(1) {
    border: none;
  }
}

.main-nav__link {
  @include link-style;
  display: block;
  padding: 20px;
  width: auto;

  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  text-align: center;

  &[href]:focus,
  &[href]:hover {
    opacity: 0.6;
  }

  &[href]:active {
    opacity: 0.3;
  }
}

@media (max-width: $mobile-width-only) {
  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}

@media (min-width: $tablet-width) {
  .main-nav {
    display: flex;
    flex-shrink: 0;
    flex-grow: 1;
    justify-content: flex-end;
    align-items: center;
    width: 510px;
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav__list {
    display: flex;
    flex-wrap: wrap;

    border: none;
  }

  .main-nav__link {
    position: relative;

    padding: 0;
    padding-top: 10px;
    padding-bottom: 10px;

    font-size: 18px;
    font-weight: 400;
    color: #222222;
    line-height: 25px;
  }

  .main-nav__item {
    margin-left: 35px;
    border: none;
  }

  .main-nav__link--current::before {
    content: "";
    position: absolute;
    bottom: 2px;
    left: 0;
    right: 0;

    width: auto;
    height: 2px;

    background-color: $color-main;
  }
}

@media (min-width: $desktop-width) {
  .main-nav {
    width: 950px;
    margin-right: 0;
  }

  .main-nav__link {
    font-size: 20px;
  }

  .main-nav--index {
    max-width: 600px;
  }

  .main-nav--index .main-nav__link {
    color: #ffffff;

    &[href]:focus,
    &[href]:hover {
      color: rgba(255, 255, 255, 0.6);
    }

    &[href]:active {
      color: rgba(255, 255, 255, 0.3);
    }
  }

  .main-nav--index .main-nav__link--current::before {
    background-color: #ffffff;
  }
}
