@keyframes emergence {
  from {
    opacity: 0;
  }

  to {
    opacity: 100%;
  }
}

.intro {
  margin: auto;
  color: #ffffff;
  margin-bottom: 22px;
}

.intro__wrap {
  position: relative;
}

.intro__image-bg {
  min-height: 305px;
  background-color: #65a93c;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.intro__title {
  margin: 0;
  padding: 26px;

  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  text-align: center;
}

.intro__text {
  margin: 0;

  font-size: 14px;
  line-height: 14px;
  text-align: center;
  text-transform: uppercase;
}

.intro__image {
  position: absolute;
  top: 166px;
  left: calc(50% - 280px / 2);

  animation-name: emergence;
  animation-duration: 2s;
  transition-timing-function: ease-out;
}

.intro__button {
  margin-top: 135px;
  padding: 11px;

  font-size: 14px;
  line-height: 18px;
}

@media (min-width: $tablet-width) {
  .intro {
    width: auto;
    min-height: 840px;
    margin-bottom: 0;
    padding-left: 30px;
    padding-right: 30px;

    color: $text-color-main;
  }

  .intro__wrap {
    min-height: 260px;
    padding: 0;
    padding-left: 89px;
    margin-bottom: 0;

    background: none;
  }

  .intro__title {
    width: 457px;
    padding-left: 0;
    padding-top: 30px;

    font-size: 60px;
    line-height: 60px;
    text-align: left;
  }

  .intro__text {
    margin-bottom: 33px;
    padding-top: 15px;
    padding-bottom: 15px;

    font-size: 20px;
    line-height: 20px;
    text-align: left;
  }

  .intro__image-bg {
    min-height: 0;
    background: none;
  }

  .intro__image {
    display: block;
    z-index: -2;
    top: 231px;
    left: calc(50% - 709px / 2);

    width: 709px;
    height: 609px;
  }

  .intro__button {
    margin: 0;
    padding: 17px;
    width: 245px;

    font-size: 20px;
  }
}

@media (min-width: $desktop-width) {
  .intro {
    display: flex;
    min-height: 696px;
    padding-top: 144px;
    padding-right: 0;
    padding-left: 0;
    margin-bottom: 80px;
  }

  .intro__wrap {
    width: 650px;
    margin-left: calc((100vw - 1300px) / 2);
    padding-left: 120px;
    flex-grow: 0;
    flex-shrink: 0;
  }

  .intro__title {
    margin-top: 48px;
  }

  .intro__text {
    margin-bottom: 38px;
  }

  .intro__bg {
    width: 100%;
    margin-top: -144px;
    min-height: 696px;

    background-color: $color-main;
    background-image: url("../../img/index-background-desktop@1x.jpg");
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
  }

  .intro__image {
    z-index: 0;
    top: -2px;
    left: 490px;

    width: auto;
    height: auto;
  }
}
