@mixin text-mobile {
  margin: 0;

  color: #444444;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}

@mixin text-tablet {
  font-size: 16px;
  line-height: 24px;
}

@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin link-style {
  color: inherit;
  text-transform: uppercase;
  text-decoration: none;
}

@mixin mobile-center {
  margin: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@mixin tablet-center {
  width: $tablet-width;
  margin: auto;
  padding-left: 30px;
  padding-right: 30px;
}

@mixin desktop-center {
  width: $desktop-width;
  margin: auto;
  padding-left: 40px;
  padding-right: 40px;
}
